import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
//import PropTypes from "prop-types";

//import Drawer from "./drawer";
//import BackButton from "./backButton";
import RecipesList from "./recipesList";

//import { asyncLocalStorage } from "../utils/asyncLocalStorage";

//import store from "../store/store";
//import { addCategory, deleteCategory } from "../ducks/categories";
//import { changeCategory, resetCategory } from "../ducks/selected-category";
//import { addRecipe, deleteRecipe } from "../ducks/recipes";
//import { saveTempData, resetTempData } from "../ducks/temp-data";
//import { addToMenu, deleteFromMenu, resetMenu } from "../ducks/menu";

import settings from "../config";

const mapStateToProps = state => {
    return {
        recipes: state.recipes,
        categories: state.categories,
        menu: state.menu
    };
};


class MenuCategory extends Component {
    render() {

        let table = [];
        let itemsPerRow = 2;

        for (let i = 0; i < Math.round(this.props.recipes.length / itemsPerRow); i++) {
            let row = [];
            for (let j = 0; j < itemsPerRow; j++) {
                row.push(this.props.recipes[i * itemsPerRow + j]);
            }
            table.push(row);
        }

        return (
            <div>
                {table.map((item, index) => (
                    <MenuRecipe 
                        key={index}
                        recipes={this.props.recipes}
                        rowIndex={index}
                        rowData={item}
                        addTitle={this.props.addTitle}
                        addStyle={this.props.addStyle}
                        addIcon={this.props.addIcon}
                    />                    
            ))}
            </div>
        )
    }
}


class MenuRecipe extends Component {
    render() {
        return (
            <div className="category__list-row">
                {this.props.rowData.map((elem, index) => (
                    <Link
                        style={{visibility: elem === undefined ? 'hidden' : 'visible'}} 
                        key={this.props.rowIndex * 2 + index}
                        className="category__list-item" 
                        to={(elem === undefined) ? '/' : "/recipe/" + this.props.recipes[this.props.rowIndex * 2 + index].id}>
                        <div className="category__list-image" style={this.props.addStyle(this.props.recipes[this.props.rowIndex * 2 + index], this.props.rowIndex * 2 + index)}></div>
                        <div className="category__list-info-wrapper">
                            <div className="category__list-icon" style={this.props.addIcon(this.props.recipes[this.props.rowIndex * 2 + index])}></div>
                            <div className="category__list-title">{this.props.addTitle(this.props.recipes[this.props.rowIndex * 2 + index])}</div>
                        </div>
                    </Link>
                ))}
            </div>
        )
    }
}



class Menu extends Component {
    constructor(props) {
        super(props);

        this.state = {
            value: '',
            startButton: true,
            headerMenu: false,
            menu: this.props.menu.array,
            selectedCategory: settings.menuCategory,
            selectedCategoryRecipes: this.props.recipes.array.filter(elem => this.props.menu.array.indexOf(elem.id) !== -1),
        };
    }

    componentWillMount = () => {
        let categories = this.state.selectedCategoryRecipes.map(elem => elem = elem.category);
        this.setState({
            selectedCategoriesList: this.props.categories.array.filter(elem => categories.indexOf(elem.id) !== -1)
        })
    }

    toggleHeaderMenu = () => {
        this.setState({
            headerMenu: !this.state.headerMenu,
        })
    }

    drawCategoryIcon = (category) => {
        return ({
            WebkitMaskImage: "url(" + category.icon + ")",
            backgroundColor: category.color 
        })
    }

    writeRecipesCount = (id) => {
        let count = 0;
            
        this.state.selectedCategoryRecipes.forEach(item => {
            if (item.category === id) return count++;
        });

        return this.correctlyWrite(count);
    }

    correctlyWrite = (number) => {
        if (number % 10 === 1) return number + " рецепт";
        if (number % 10 > 1 && number % 10 < 5 && number > 20) return number + " рецепта";
        if (number > 1 && number < 5) return number + " рецепта";
        return number + " рецептов";
    }

    addStyle = item => {
        if (item) return ({
            backgroundImage: (item.image === '') ? 'url(' + settings.defaultCategory.image + ')' : item.image,
            backgroundColor: this.state.selectedCategory.color
        })
    }

    addTitle = item => {
        if (item) return item.title;
    }

    addIcon = item => {
        if (item) {
            let category = this.state.selectedCategoriesList.filter(elem => elem.id === item.category)[0];
            return ({
                backgroundColor: category.color,
                WebkitMaskImage: "url(" + category.icon + ")", 
            });
        }
    }
   
    render() {
        let categoryColor = this.state.selectedCategory.color;

        return (
           <div className="wrapper">
                <div className="category__header header" style={{backgroundColor: categoryColor}} >
                <div className="category__header-left-menu">
                     <Link 
                        className="back-btn" 
                        to="/"
                    />
                </div>
                <div className="category__header-right-menu">
                    <div className="category__header-menu-btn dots-menu-btn" onClick={this.toggleHeaderMenu}></div>
                </div>
            </div>
            <div className="category__body">
                <div className="category__title">
                    <div className="category__selected">
                        <div className="category__selected-icon" style={this.drawCategoryIcon(this.state.selectedCategory)}></div>
                        <div className="category__selected-title" style={{color: categoryColor}}>{this.state.selectedCategory.name}</div>
                    </div>
                    <div className="category__items-count-wrapper">
                        <div className="category__items-count" style={{backgroundColor: categoryColor}}>{this.writeRecipesCount(this.state.selectedCategory.id)}</div>
                    </div>
                </div>
                <MenuCategory 
                    recipes={this.state.selectedCategoryRecipes}
                    addTitle={this.addTitle}
                    addStyle={this.addStyle}
                    addIcon={this.addIcon}
                />
            </div>
        </div>
        );
    }
}


/*Game.propTypes = {
    location: PropTypes.string
};*/

export default connect(mapStateToProps)(Menu);



